<template>
	<div ref="wrapper">
		<slot></slot>
	</div>
</template>

<script>
	import BScroll from 'better-scroll'
	export default {
		name: 'scroll',
		props: {
			/**
			 * 1 滚动的时候会派发scroll事件，会截流。
			 * 2 滚动的时候实时派发scroll事件，不会截流。
			 * 3 除了实时派发scroll事件，在swipe的情况下仍然能实时派发scroll事件
			 */
			probeType: {
			  type: Number,
			  default: 2
			},
			/**
			 * 点击列表是否派发click事件
			 */
			click: {
			  type: Boolean,
			  default: true
			},
			/**
			 * 是否开启横向滚动
			 */
			scrollX: {
			  type: Boolean,
			  default: false
			},
			/**
			 * 是否派发滚动事件
			 */
			listenScroll: {
			  type: Boolean,
			  default: false
			},
			/**
			 * 列表的数据
			 */
			data: {
			  // 产品展示数据(重要必须传值)
			  type: Array,
			  default: null
			},
			/**
			 * 是否派发滚动到底部的事件，用于上拉加载
			 */
			pullup: {
			  type: Boolean,
			  default: false
			},
			/**
			 * 是否派发顶部下拉的事件，用于下拉刷新
			 */
			pulldown: {
			  type: Boolean,
			  default: false
			},
			/**
			 * 是否派发列表滚动开始的事件
			 */
			beforeScroll: {
			  type: Boolean,
			  default: false
			},
			/**
			 * 当数据更新后，刷新scroll的延时。
			 */
			refreshDelay: {
			  type: Number,
			  default: 20
			}
		},
		mounted() { // DOM渲染完毕后
			setTimeout(() => {
				this._initScroll()
			},20)
		},
		methods: {
			_initScroll() {
				if (!this.$refs.wrapper) {
					return
				}
				this.scroll = new BScroll(this.$refs.wrapper, {
					probeType: this.probeType,
					click: this.click,
					scrollX: this.scrollX
				})
				// 是否派发滚动事件
				if (this.listenScroll) {
				  this.scroll.on("scroll", pos => {
				    this.$emit("scroll", pos); // 子传父
				  });
				}
				// 是否派发滚动到底部事件，用于上拉加载
				if (this.pullup) {
					this.scroll.on('scrollEnd', () => {
						if (this.scroll.y <= this.scroll.maxScrollY + 100) {
							this.$emit('pullup')
						}
					})
				}
				// 是否派发顶部下拉事件，用于下拉刷新
				if (this.pulldown) {
					this.scroll.on('touchend', pos => {
						if (pos.y > 50) {
							this.$emit('pulldown')
						}
					})
				}
				// 是否派发列表滚动开始的事件
				if (this.beforeScroll) {
					this.scroll.on('beforeScrollStart', () => {
						this.$emit('beforeScroll')
					})
				}
			},
			disable() {
			  // 代理better-scroll的disable方法
			  this.scroll && this.scroll.disable();
			},
			enable() {
			  // 代理better-scroll的enable方法
			  this.scroll && this.scroll.enable();
			},
			refresh() {
				// 刷新(重要)
				// 代理better-scroll的refresh方法
				this.scroll && this.scroll.refresh()
			},
			scrollTo() {
			  // 滚动到 某个(x,y)坐标(重要)
			  // 代理better-scroll的scrollTo方法
			  this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments);
			},
			scrollToElement() {
			  // 代理better-scroll的scrollToElement方法
			  this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments);
			}
		}
	}
</script>

<style scoped>
</style>




